import { useParams } from "react-router-dom";
import { Logo } from "../components/Logo";
import { Sidebar } from "../components/Sidebar";
import logoVideo from "../components/Items/logo_background.mp4";
import { doc, getDoc } from "firebase/firestore";
import { db } from "../firebase";
import { useEffect, useState } from "react";
import styled from "styled-components";

export function Post({ isFixed, targetComponentRef }) {
  let {id} = useParams();
  const [data, setData] = useState(null);

  const getDocument = async () => {
    const docRef = doc(db, 'blogging', id);  //   
    const docSnap = await getDoc(docRef);    //  

    if (docSnap.exists())
      return docSnap.data();  //   
  }

  useEffect(() => {
    const fetchData = async () => {
      const docData = await getDocument();  //    
      setData(docData);  //  
    };
    
    fetchData();  //  
  }, []);  //   id 


  function postWriter(){
    return(
      <div className="logo-text">
        <CategoryContainer>
          <CategoryBox>{data?.category.prev} - {data?.category.current}</CategoryBox>
        </CategoryContainer>
        <h1>{data?.title}</h1>
        <MetaContainer>
          <MetaItem>DEVH</MetaItem> 
          <MetaItem>{new Date(data?.date.toDate()).toLocaleString()}</MetaItem>
        </MetaContainer>
      </div>
    )
  }

  return (
    <div className="container">
      <div className="body">
        <Logo 
          isFixed={isFixed} 
          background={logoVideo} 
          writer={postWriter()}/>
        <main className="main" ref={targetComponentRef}>
          <div className="wrapper">
            {data?.contents}
          </div>
        </main>
        <footer></footer>
      </div>
      <Sidebar />
    </div>
  );
}

const CategoryContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  font-size: 18px;
  margin-bottom: 1em;
`

const CategoryBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: cornflowerblue;
  border-radius: 3em;
  padding: 0.4em 0.9em;
`

const MetaContainer = styled.div`
display: flex;
flex-direction: row;
justify-content: center;
align-items: center;
`

const MetaItem = styled.span`
  font-size: 16px;
  &:nth-child(1)::after{
    content: '\t\t'; 
    margin: 0 8px; /*      */
  }
`